/*
  This boilerplate is based in ITCSS and SMACSS
*/
@import url("https://fonts.googleapis.com/css?family=Poppins");
@import url("https://fonts.googleapis.com/css?family=Open+Sans:700");
*,
*::before,
*:after {
  box-sizing: border-box;
}

body {
  margin: 0;
  line-height: 1.6;
  font-family: "Poppins", sans-serif;
  background-color: #FCFCFC;
  color: #53575A;
}

h1, h2, h3, h4, h5, h6 {
  font-family: "Open Sans", sans-serif;
  margin: 0;
}

p {
  margin: 0;
}

img {
  max-width: 100%;
  width: 100%;
  display: block;
}

.gallery {
  position: relative;
}

.gallery-figure {
  text-align: center;
  border-radius: 5px;
  position: absolute;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  margin: 0;
}

.gallery-description {
  color: #DD3F3B;
  font-weight: 600;
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 100%;
  transform: translate(-50%);
  background: rgba(0, 0, 0, 0.5);
  border-top: 3px solid rgba(0, 0, 0, 0.2);
}

.gallery-navigation__counter {
  font-size: .8em;
}

.gallery-image {
  transition: all 1s ease;
  height: 100%;
  object-fit: cover;
}

.gallery-image.animated {
  animation: opacity .5s linear alternate;
}

.gallery-image.animated-r {
  animation: opacity .5s linear alternate;
}

@keyframes opacity {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.next {
  position: absolute;
  top: 45%;
  right: 0;
  opacity: 1;
}

.next.disabled {
  opacity: 0;
}

.next::after {
  content: url("https://image.flaticon.com/icons/svg/180/180000.svg");
  position: absolute;
  top: 0;
  right: 0;
  width: 35px;
  height: 35px;
  z-index: 10;
  background: rgba(0, 0, 0, 0.4);
  border-radius: 50%;
}

.prev {
  position: absolute;
  top: 45%;
  left: 0;
}

.prev::after {
  content: url("https://image.flaticon.com/icons/svg/179/179983.svg");
  position: absolute;
  top: 0;
  left: 10%;
  width: 35px;
  height: 35px;
  z-index: 10;
  background: rgba(0, 0, 0, 0.4);
  border-radius: 50%;
}
